.bus-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    margin: 20px;
    font-family: sans-serif;
  }
  
  .row {
    display: flex;
    justify-content: center;
    gap: 8px;
  }
  
  .driver-row .seat {
    background-color: #ccc;
    cursor: default;
  }
  
  .seat {
    width: 40px;
    height: 40px;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    cursor: pointer;
    user-select: none;
    border: 2px solid #333;
  }
  
  .aisle {
    width: 30px;
  }
  
  .driver {
    background-color: #222;
    color: white;
  }
  
  .available {
    background-color: #4caf50;
  }
  
  .occupied {
    background-color: #f44336;
    cursor: not-allowed;
  }
  
  .selected {
    background-color: #ffeb3b;
  }
  
  .back-row .seat {
    margin: 0 2px;
  }
  
  .selected-info {
    margin-top: 16px;
    font-weight: bold;
  }
  